<template>
  <div class="row pt-3">
    <div class="offset-sm-4 col-sm-4">
      <div class="box">
        <h4 class="text-center font-weight-normal mt-2"></h4>
        <form @submit.prevent="submit">
          <div class="form-group">
            <label><small></small></label>
            <input type="text" class="form-control" v-model="username" required>
          </div>
          <div class="form-group">
            <label></label>
            <input type="password" class="form-control" v-model="password" required>
          </div>
          <button type="submit" class="my-2 btn btn-primary w-100"></button>
        </form>
        <div class="mb-2 d-flex justify-content-around">
          <!--<socialite-btns></socialite-btns>-->
        </div>
      </div>
    </div>
    <div class="offset-sm-4 col-sm-4 text-center mt-2 d-flex justify-content-between">
      <p> <router-link class="text-blue" :to="{ name: 'auth.register' }"></router-link>
      </p>
      <p>
        <router-link class="text-blue" :to="{ name: 'auth.forget-password' }"></router-link>
      </p>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import SocialiteBtns from '$components/socialite-login'
export default {
  data () {
    return {
      username: '',
      password: ''
    }
  },
  components: { SocialiteBtns },
  methods: {
    ...mapActions(['attemptLogin']),

    async submit () {
      const { username, password } = this

      try {
        await this.attemptLogin({ username, password })

        this.$message.success('~')
        this.$router.push({ name: 'home' })
      } catch (e) {
        if (e.status !== 422) {
          this.$message.error('')
        }
      }
    }
  }
}
</script>
